.block-centered-content {
  width: 100%;
  padding: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(var(--light-grey), 1);
}
@media (min-width: 1024px) {
  .block-centered-content {
    padding: 120px 0;
  }
}
.block-centered-content[data-visibility=visible] .block-content {
  opacity: 1;
}
.block-centered-content .block__inner {
  max-width: 1800px;
  width: 95%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-centered-content .block-content {
  max-width: 920px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 5rem;
  opacity: 0;
  transition: 0.5s ease-in-out;
}
.block-centered-content .block-content .block-title {
  font-family: var(--adore);
  font-size: 8.2rem;
  font-weight: 700;
  line-height: 1.2;
  text-align: center;
}
@media (max-width: 1023px) {
  .block-centered-content .block-content .block-title {
    font-size: 36px;
  }
}
.block-centered-content .block-content .block-menu {
  width: 80%;
  margin-bottom: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-centered-content .block-content .block-menu ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;
}
.block-centered-content .block-content .block-menu ul li {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-centered-content .block-content .block-menu ul li a {
  padding: 2.5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--montserrat);
  font-size: 1.8rem;
  line-height: 1.2;
  color: rgba(var(--text-color), 1);
  transition: 0.3s ease;
}
.block-centered-content .block-content .block-menu ul li a:hover {
  color: rgba(var(--secondary-color), 1);
}